import React from 'react';
import PropTypes from 'prop-types';

import classNames from 'classnames';
import Transition from 'react-transition-group/Transition';

import { mergeProps, unMergeProps, TransitionPropTypeKeys, TransitionTimeouts } from './utils';

const Animate = props => {
  const {
    hook: Hook,
    baseClass,
    baseClassActive,
    className,
    children,
    innerRef,
    ...otherProps
  } = props;
  const transitionProps = mergeProps(otherProps, TransitionPropTypeKeys);
  const childProps = unMergeProps(otherProps, TransitionPropTypeKeys);

  return (
    <Transition {...transitionProps}>
      {status => {
        const isActive = status === 'entered';
        const classes = classNames(className, baseClass, isActive && baseClassActive);
        return (
          <Hook className={classes} {...childProps} ref={innerRef}>
            {children}
          </Hook>
        );
      }}
    </Transition>
  );
};

Animate.propTypes = {
  ...Transition.propTypes,
  children: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.node), PropTypes.node]),
  hook: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
  baseClass: PropTypes.string,
  baseClassActive: PropTypes.string,
  className: PropTypes.string,
  innerRef: PropTypes.oneOfType([PropTypes.object, PropTypes.string, PropTypes.func]),
};

Animate.defaultProps = {
  ...Transition.defaultProps,
  hook: 'div',
  baseClass: 'fade',
  baseClassActive: 'in',
  timeout: TransitionTimeouts.Fade || 150,
  appear: true,
  enter: true,
  exit: true,
  in: true,
};

export default Animate;



