import React from 'react';

import classNames from 'classnames';
import { Modal, ModalHeader, ModalBody, ModalFooter } from '../src';

class ModalDocs extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: false,
    };

    this.toggle = this.toggle.bind(this);
  }

  toggle() {
    this.setState({
      modal: !this.state.modal,
    });
  }

  render() {
    return (
      <div className="container">
        <section className="section">
          <div className="row">
            <div className="col-md-6">
              <h2>Modal</h2>
            </div>
            <div className={classNames('col-md-6', 'text-xs-right')}>
              <button className="btn btn-default" onClick={this.toggle}>
                Click Me
              </button>
            </div>
            <Modal isOpen={this.state.modal} toggle={this.toggle}>
              <ModalHeader toggle={this.toggle}>Modal title</ModalHeader>
              <ModalBody>
                Attempted murder? Now honestly, what is that? Do they give a Nobel Prize for
                attempted chemistry? The Internet King? Books are useless! I only ever read one
                book, To Kill A Mockingbird, and it gave me absolutely no insight on how to kill
                mockingbirds! Sure it taught me not to judge a man by the color of his skin...but
                what good does *that* do me?
              </ModalBody>
              <ModalFooter>
                <button className="btn btn-primary" onClick={this.toggle}>
                  Do Something
                </button>{' '}
                <button className="btn btn-secondary" onClick={this.toggle}>
                  Cancel
                </button>
              </ModalFooter>
            </Modal>
          </div>
        </section>
      </div>
    );
  }
}

export default ModalDocs;



