import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

import Animate from './Animate';
import {
  getOriginalBodyPadding,
  conditionallyUpdateScrollbar,
  setScrollbarWidth,
  unMergeProps,
  TransitionTimeouts,
} from './utils';

function noop() {}

const FadePropTypes = PropTypes.shape(Animate.propTypes);

const propTypes = {
  isOpen: PropTypes.bool,
  isDialogCentered: PropTypes.bool,
  modalSize: PropTypes.oneOf(['lg', 'md', 'sm']),
  toggle: PropTypes.func,
  // a11y related props
  labelledBy: PropTypes.string,
  role: PropTypes.string,
  // a11y ends
  backdrop: PropTypes.oneOfType([PropTypes.bool, PropTypes.oneOf(['static'])]),
  onEnter: PropTypes.func,
  onExit: PropTypes.func,
  onOpened: PropTypes.func,
  onClosed: PropTypes.func,
  children: PropTypes.node,
  className: PropTypes.string,
  modalClassName: PropTypes.string,
  fade: PropTypes.bool,
  zIndex: PropTypes.number,
  backdropTransition: FadePropTypes,
  modalTransition: FadePropTypes,
  innerRef: PropTypes.oneOfType([PropTypes.object, PropTypes.string, PropTypes.func]),
};

const propsToUnMerge = Object.keys(propTypes);

const defaultProps = {
  isOpen: false,
  isDialogCentered: false,
  role: 'dialog',
  backdrop: true,
  zIndex: 1050,
  fade: true,
  // Extra props
  modalSize: 'md',
  toggle: noop,
  labelledBy: undefined,
  onEnter: noop,
  onExit: noop,
  children: null,
  className: null,
  modalClassName: null,
  onOpened: noop,
  onClosed: noop,
  innerRef: null,
  // Extra props end
  modalTransition: {
    timeout: TransitionTimeouts.Modal,
  },
  backdropTransition: {
    mountOnEnter: true,
    timeout: TransitionTimeouts.Fade, // uses standard fade transition
  },
};

class Modal extends React.Component {
  constructor(props) {
    super(props);
    this.modalElement = null;
    this.modalOriginalBodyPadding = null;

    this.onOpened = this.onOpened.bind(this);
    this.onClosed = this.onClosed.bind(this);
    this.handleBackdropClick = this.handleBackdropClick.bind(this);
    this.handleBackdropMouseDown = this.handleBackdropMouseDown.bind(this);
    this.handleEscape = this.handleEscape.bind(this);

    this.state = {
      isOpen: props.isOpen,
    };

    if (props.isOpen) {
      this.init();
    }
  }

  componentDidMount() {
    if (this.props.onEnter) {
      this.props.onEnter();
    }

    if (this.state.isOpen) {
      this.setFocus();
    }

    this.isModalMounted = true;
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.isOpen && !this.props.isOpen) {
      this.setState({ isOpen: nextProps.isOpen });
    }
  }

  componentWillUpdate(nextProps, nextState) {
    if (nextState.isOpen && !this.state.isOpen) {
      this.init();
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.state.isOpen && !prevState.isOpen) {
      this.setFocus();
    }

    if (this.modalElement && prevProps.zIndex !== this.props.zIndex) {
      this.modalElement.style.zIndex = this.props.zIndex;
    }
  }

  componentWillUnmount() {
    if (this.props.onExit) {
      this.props.onExit();
    }

    if (this.state.isOpen) {
      this.destroy();
    }

    this.isModalMounted = false;
  }

  onOpened(node, isAppearing) {
    this.props.onOpened();
    (this.props.modalTransition.onEntered || noop)(node, isAppearing);
  }

  onClosed(node) {
    // so all methods get called before it is unmounted
    this.props.onClosed();
    (this.props.modalTransition.onExited || noop)(node);
    this.destroy();

    if (this.isModalMounted) {
      this.setState({ isOpen: false });
    }
  }

  setFocus() {
    if (
      this.modalDialog &&
      this.modalDialog.parentNode &&
      typeof this.modalDialog.parentNode.focus === 'function'
    ) {
      this.modalDialog.parentNode.focus();
    }
  }

  // not mouseUp because scrollbar fires it, shouldn't close when user scrolls
  handleBackdropClick(e) {
    if (e.target === this.mouseElem) {
      e.stopPropagation();
      if (!this.props.isOpen || this.props.backdrop !== true) return;

      const container = this.modalDialog;

      if (e.target && !container.contains(e.target) && this.props.toggle) {
        this.props.toggle(e);
      }
    }
  }

  handleBackdropMouseDown(e) {
    this.mouseElem = e.target;
  }

  handleEscape(e) {
    if (this.props.isOpen && e.keyCode === 27 && this.props.toggle) {
      this.props.toggle(e);
    }
  }

  init() {
    try {
      this.modalTrigger = document.activeElement;
    } catch (err) {
      this.modalTrigger = null;
    }
    this.modalElement = document.createElement('div');
    this.modalElement.setAttribute('tabindex', '-1');
    this.modalElement.style.position = 'relative';
    this.modalElement.style.zIndex = this.props.zIndex;
    this.modalOriginalBodyPadding = getOriginalBodyPadding();

    conditionallyUpdateScrollbar();

    document.body.appendChild(this.modalElement);
    if (!this.bodyClassAdded) {
      document.body.className = classNames(document.body.className, 'modal-open');
      this.bodyClassAdded = true;
    }
  }

  destroy() {
    if (this.modalElement) {
      document.body.removeChild(this.modalElement);
      this.modalElement = null;
    }

    if (this.modalTrigger) {
      this.modalTrigger.focus();
      this.modalTrigger = null;
    }

    if (this.bodyClassAdded) {
      const modalOpenClassName = 'modal-open';
      // Use regex to prevent matching `modal-open` as part of a different class, e.g. `my-modal-opened`
      const modalOpenClassNameRegex = new RegExp(`(^| )${modalOpenClassName}( |$)`);
      document.body.className = document.body.className
        .replace(modalOpenClassNameRegex, ' ')
        .trim();
      this.bodyClassAdded = false;
    }

    setScrollbarWidth(this.modalOriginalBodyPadding);
  }

  renderModalDialog() {
    const attributes = unMergeProps(this.props, propsToUnMerge);
    const dialogBaseClass = 'modal-dialog';
    const classes = classNames(dialogBaseClass, this.props.className, {
      [`modal-${this.props.modalSize}`]: this.props.modalSize,
      [`${dialogBaseClass}-centered`]: this.props.isDialogCentered,
    });

    return (
      <div
        {...attributes}
        className={classes}
        role="document"
        ref={c => {
          this.modalDialog = c;
        }}
      >
        <div className="modal-content">{this.props.children}</div>
      </div>
    );
  }

  render() {
    if (this.state.isOpen) {
      const { isOpen, backdrop, role, labelledBy, innerRef } = this.props;

      const modalAttributes = {
        onClick: this.handleBackdropClick,
        onMouseDown: this.handleBackdropMouseDown,
        onKeyUp: this.handleEscape,
        style: { display: 'block' },
        'aria-labelledby': labelledBy,
        role,
        tabIndex: '-1',
      };

      const hasTransition = this.props.fade;
      const modalTransition = {
        ...Animate.defaultProps,
        ...this.props.modalTransition,
        baseClass: hasTransition ? this.props.modalTransition.baseClass : '',
        timeout: hasTransition ? this.props.modalTransition.timeout : 0,
      };
      const backdropTransition = {
        ...Animate.defaultProps,
        ...this.props.backdropTransition,
        baseClass: hasTransition ? this.props.backdropTransition.baseClass : '',
        timeout: hasTransition ? this.props.backdropTransition.timeout : 0,
      };

      const Backdrop = hasTransition ? (
        <Animate {...backdropTransition} in={isOpen && !!backdrop} className="modal-backdrop" />
      ) : (
        <div className={classNames('modal-backdrop', 'show')} />
      );

      return (
        <Fragment>
          <Animate
            {...modalAttributes}
            {...modalTransition}
            in={isOpen}
            onEntered={this.onOpened}
            onExited={this.onClosed}
            className="modal"
            innerRef={innerRef}
          >
            {this.renderModalDialog()}
          </Animate>
          {Backdrop}
        </Fragment>
      );
    }

    return null;
  }
}

Modal.propTypes = propTypes;
Modal.defaultProps = defaultProps;

export default Modal;



